.introduce {
  position: relative;
  z-index: 1;

  margin: -93px 0 0 0;
  padding: 93px 0 0 0;

  background: $bg-dark-color url("../img/bg-image-desktop.png") no-repeat 0 0;
  background-size: 100% 100%;

  overflow: hidden;
}

.introduce__wrapper {
  position: relative;

  display: flex;

  margin: 0 auto;
  padding-top: 164px;
  padding-bottom: 100px;

  max-width: 1440px;
}

.introduce__text-wrapper {
  padding-left: 18px;

  width: 50%;
}

.introduce__image-wrapper {
  position: relative;
  z-index: 1;

  width: 50%;

  img {
    margin-left: 69px;

    min-width: 705px;
    height: 515px;
  }

  &::before {
    content: "";

    position: absolute;
    z-index: -1;
    top: -136px;
    left: 101px;

    width: 637px;
    height: 640px;

    background: url("../img/triangle-big.svg") no-repeat 0 0;
    background-size: 100% 100%;
  }

  &::after {
    content: "";

    position: absolute;
    z-index: -1;
    top: 128px;
    left: 68px;

    width: 373px;
    height: 377px;

    background: url("../img/triangle-medium.svg") no-repeat 0 0;
    background-size: 100% 100%;
  }
}

.introduce__header {
  margin: 60px 0 0 0;
  padding: 0;

  width: 750px;

  @media (max-width: 1250px) {
    width: 600px;
    text-align: center;
  }

  @include fontRobotoCondensed(80px, 94px);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.07em;
}

.introduce__description {
  position: relative;

  margin: 24px 0 0 0;
  padding: 0 0 0 97px;

  width: 650px;

  @media (max-width: 1250px) {
    width: 100%;
  }

  @include fontPTMono(22px, 34px, #ffffff);
  text-transform: lowercase;

  &::before {
    content: "";

    position: absolute;
    top: 17px;
    left: 3px;

    width: 66px;
    height: 2px;

    background-color: #ffffff;
  }
}

.introduce__button {
  margin: 44px 0 0 97px;

  span:last-of-type {
    display: none;
  }
}

.introduce__scroll-down-button {
  position: absolute;
  bottom: 124px;
  left: -17px;

  width: 93px;
  height: 20px;

  @include fontPTMono(14px, 16px, #ffffff);
  text-decoration: none;

  transform: rotate(-90deg);

  opacity: 0.5;

  &:hover {
    opacity: 0.3;
  }

  &:active {
    opacity: 0.2;
  }

  &:focus {
    opacity: 1;
  }
}

@media (max-width: $tablet-width) {
  .introduce {
    margin-top: -80px;
    padding-top: 80px;

    background-image: url("../img/bg-image-tablet.png");
  }

  .introduce__wrapper {
    padding-top: 30px;
    padding-bottom: 163px;

    width: 750px;

    &::before {
      display: none;
    }
  }

  .introduce__text-wrapper {
    margin-top: 79px;
    padding-left: 78px;

    width: 500px;
  }

  .introduce__image-wrapper {
    img {
      margin-top: 3px;
      margin-left: -8px;

      min-width: 465px;
      height: 340px;
    }

    &::before {
      top: -148px;
      left: -52px;

      width: 502px;
      height: 502px;

      background-size: 502px 502px;
    }

    &::after {
      top: 60px;
      left: -78px;

      width: 294px;
      height: 294px;

      background-size: 294px 294px;
    }
  }

  .introduce__header {
    margin-top: 0;

    width: 400px;

    font-size: 60px;
    line-height: 70px;
    text-align: left;
  }

  .introduce__description {
    width: 360px;

    margin-top: 28px;
    padding-left: 50px;

    font-size: 18px;
    line-height: 156.8%;

    &::before {
      width: 33px;
    }
  }

  .introduce__button {
    margin: 55px 0 0 50px;
  }

  .introduce__scroll-down-button {
    display: none;
  }
}

@media (max-width: $mobile-width) {
  .introduce {
    margin-top: -61px;
    padding-top: 61px;

    background-image: url("../img/bg-image-mobile.png");
  }

  .introduce__wrapper {
    flex-direction: column-reverse;

    padding-top: 25px;
    padding-bottom: 69px;

    width: 320px;
  }

  .introduce__text-wrapper {
    z-index: 5;

    margin-top: -30px;
    padding-left: 15px;

    width: 320px;
  }

  .introduce__image-wrapper {
    img {
      margin-top: 6px;
      margin-left: 25px;

      min-width: 295px;
      height: 220px;
    }

    &::before {
      top: -131px;
      left: 177px;

      width: 479.4px;
      height: 479.4px;

      background-size: 479.4px 479.4px;
    }

    &::after {
      top: 68px;
      left: 152px;

      width: 281px;
      height: 281px;

      background-size: 281px 281px;
    }
  }

  .introduce__header {
    width: 250px;

    font-size: 40px;
    line-height: 47px;
  }

  .introduce__description {
    width: 245px;

    font-size: 16px;

    &::before {
      top: 15px;
    }
  }

  .introduce__button {
    margin: 43px 0 0 2px;

    span:first-of-type {
      display: none;
    }

    span:last-of-type {
      display: inline;
    }
  }
}
